.container {
    position: relative;
    overflow: hidden;
  }

.gradient-blur-right {
position: absolute;
z-index: 5;
inset: auto 0 0 0;
height: 100%;
width: 30%;
right: -4rem;
margin-left: auto;
pointer-events: none;
}

.gradient-blur-right > div,
.gradient-blur-right::before,
.gradient-blur-right::after {
position: absolute;
inset: 0;
}

.gradient-blur-right::before {
content: "";
z-index: 1;
backdrop-filter: blur(0.5px);
mask: linear-gradient(
    to right,
    rgba(255, 255, 255, 0) 0%,
    rgba(255, 255, 255, 1) 12.5%,
    rgba(255, 255, 255, 1) 25%,
    rgba(255, 255, 255, 0) 37.5%
);
}

.gradient-blur-right > div:nth-of-type(1) {
z-index: 2;
backdrop-filter: blur(1px);
mask: linear-gradient(
    to right,
    rgba(255, 255, 255, 0) 12.5%,
    rgba(255, 255, 255, 1) 25%,
    rgba(255, 255, 255, 1) 37.5%,
    rgba(255, 255, 255, 0) 50%
);
}

.gradient-blur-right > div:nth-of-type(2) {
z-index: 3;
backdrop-filter: blur(2px);
mask: linear-gradient(
    to right,
    rgba(255, 255, 255, 0) 25%,
    rgba(255, 255, 255, 1) 37.5%,
    rgba(255, 255, 255, 1) 50%,
    rgba(255, 255, 255, 0) 62.5%
);
}

.gradient-blur-right > div:nth-of-type(3) {
z-index: 4;
backdrop-filter: blur(4px);
mask: linear-gradient(
    to right,
    rgba(255, 255, 255, 0) 37.5%,
    rgba(255, 255, 255, 1) 50%,
    rgba(255, 255, 255, 1) 62.5%,
    rgba(255, 255, 255, 0) 75%
);
}

.gradient-blur-right > div:nth-of-type(4) {
z-index: 5;
backdrop-filter: blur(8px);
mask: linear-gradient(
    to right,
    rgba(255, 255, 255, 0) 50%,
    rgba(255, 255, 255, 1) 62.5%,
    rgba(255, 255, 255, 1) 75%,
    rgba(255, 255, 255, 0) 87.5%
);
}

.gradient-blur-right > div:nth-of-type(5) {
z-index: 6;
backdrop-filter: blur(16px);
mask: linear-gradient(
    to right,
    rgba(255, 255, 255, 0) 62.5%,
    rgba(255, 255, 255, 1) 75%,
    rgba(255, 255, 255, 1) 87.5%,
    rgba(255, 255, 255, 0) 100%
);
}

.gradient-blur-right > div:nth-of-type(6) {
z-index: 7;
backdrop-filter: blur(32px);
mask: linear-gradient(
    to right,
    rgba(255, 255, 255, 0) 75%,
    rgba(255, 255, 255, 1) 87.5%,
    rgba(255, 255, 255, 1) 100%
);
}

.gradient-blur-right::after {
content: "";
z-index: 8;
backdrop-filter: blur(64px);
mask: linear-gradient(
    to right,
    rgba(255, 255, 255, 0) 87.5%,
    rgba(255, 255, 255, 1) 100%
);
}
